<template>
  <div class="bg-white rounded-md p-3">
    <PageTitle> Markaz tomonidan nazoratsiz / qarovsiz aniqlangan bolani taqsimlash </PageTitle>
    <section class="my-4">
      <AllocatedJuvenilesTableWrapper v-model:selected-juvenile="selectedJuvenile" :update-juveniles-list="updateJuvenilesList" :fetch-juveniles-list-handler="fetchAcceptedJuvenilesList" />
      <span class="error_message">{{ selectedJuvenileError }}</span>
    </section>
    <section v-if="selectedJuvenile" class="my-4">
      <h1 class="text-2xl my-4">Tanlangan bolaning shaxsiy ma'lumotlari</h1>
      <div class="flex">
        <PassportCard :passport-info="selectedJuvenile" @canceled="setSelectedJuvenile(null)" />
      </div>
    </section>
    <section class="my-4" v-if="selectedJuvenile">
      <h1 class="text-2xl my-4">Taqsimot ma'lumotlari</h1>
      <AllocateJuvenileForm :clear-form-mode="clearFormFields" @submit="handleSubmit" />
    </section>
    <ModalComponent ref="modalComponentRef" modalClass="!w-auto" modal-position="center" :close-on-click-away="false" :hide-close-button="true">
      <template #slotBody>
        <div class="p-10 text-3xl flex items-center justify-center">
          <h1>Bola muvaffaqiyatli taqsimlandi</h1>
          <Icon icon="mdi:check-circle" class="mx-2 text-primary" width="34" height="34" />
        </div>
        <div class="flex justify-center">
          <button class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80" @click="modalComponentRef.closeModal">Davom etish</button>
          <button class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80" @click="$router.push('/')">Bosh sahifa</button>
        </div>
      </template>
    </ModalComponent>
  </div>
</template>

<script setup>
import {ref, watch} from 'vue';
import PassportCard from '../components/PassportCard';
import useAlert from '../composables/useAlert';
import AllocatedJuvenilesTableWrapper from '../components/AllocatedJuvenilesTableWrapper';
import AllocateJuvenileForm from '../components/AllocateJuvenileForm';
import PageTitle from '../components/LayoutItems/PageTitle';
import AllocateService from '../services/allocate.service';
import useErrorAlert from '../composables/useErrorAlert';
import ModalComponent from '../components/Common/Modal/ModalComponent.vue';

const selectedJuvenile = ref(null);
const selectedJuvenileError = ref('');
const clearFormFields = ref(false);

const modalComponentRef = ref()

const {fetchAcceptedJuvenilesList} = AllocateService;
const {alertIt} = useAlert();
const {alertThisError} = useErrorAlert();
const {sendJuvenileFromCenter} = AllocateService;

const updateJuvenilesList = ref(false);
const refreshJuvenileList = () => {
  updateJuvenilesList.value = true;
};

async function handleSubmit(formData) {
  try {
    if (!selectedJuvenile.value) {
      selectedJuvenileError.value = 'Bola tanlanishi shart!';
      return null;
    }
    const {status, data} = await sendJuvenileFromCenter(formData, selectedJuvenile.value.id);
    if (status === 200 || status === 201) {
      modalComponentRef.value.openModal()
      alertIt(data?.message, 'success')
    }
    clearAllFields();
    refreshJuvenileList();
  } catch (e) {
    alertThisError(e);
  }
}

function clearAllFields() {
  selectedJuvenile.value = null;
  clearFormFields.value = true;
}

watch(
  () => selectedJuvenile.value,
  (val) => {
    if (val && selectedJuvenileError.value) {
      selectedJuvenileError.value = '';
    }
  }
);

function setSelectedJuvenile(juvenile) {
  selectedJuvenile.value = juvenile;
}
</script>

<style scoped></style>
